.header {
    box-sizing: border-box;
    width: 100%;
    height: 418px;
    background-color: lightcoral;
    display: flex;
    flex-direction: column;
}

.headerAdopt {
    box-sizing: border-box;
    width: 100%;
    /* height: 180px; */
    background-color: rgb(249, 231, 159);
    display: flex;
    justify-content: center;
}

.headerGiveAway {
    box-sizing: border-box;
    width: 100%;
