.dogpage-wrapper {
    box-sizing: border-box;
    width: 100%;
}

.dogpage-image-wrapper {
    width: 100%;
    height: 500px;
    overflow: hidden;
}

.dogpage-image {
    width: 100%;
    background-position: center;
}

.dogpage-info-wrapper {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin: 0 50px;
}

.dogpage-info-wrapper div {
    width: 35%;
    margin-right: 40px;
}

.dogpage-info-wrapper div:first-child {
    width: 30%;
}

.meter {
    height: 10px;
	position: relative;
	background: #222222;
	-moz-border-radius: 25px;
	-webkit-border-radius: 25px;
	border-radius: 25px;
    padding: 5px;
    margin: 10px 0;
	box-shadow: inset 0 -1px 1px rgba(255,255,255,0.3);
}

.meter > span {
    display: block;
    height: 100%;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    background-color: #ffffff;
    background-image: linear-gradient(
      center bottom,
      rgb(43,194,83) 37%,
      rgb(84,240,84) 69%
    );
    box-shadow: 
      inset 0 2px 9px  rgba(255,255,255,0.3),
      inset 0 -2px 6px rgba(0,0,0,0.4);
    position: relative;
    overflow: hidden;
  }

.sticker {
    width: fit-content;
    font-size: 14px;
    color: #ffffff;
    padding: 5px 11px;
    margin: 7px 0;
    border-radius: 25px;
    text-align: center;
}

.sticker-negative {
    font-size: 14px;
    text-decoration: line-through;
    padding: 5px 11px;
}

.cat {
    background-color: #FF5733;
}

.child {
    background-color: #C70039;
}

.walks {
    background-color: #900C3F;
}